#colaborador{
    display:block!important;
}

.blocks-color{
    padding-top:1rem;
}

.blocks-color .special-block{
    padding: 120px 0 90px 0;
    background: url(bubbles-blanco.png) repeat-x #B4B0A7;
    position:relative;
    h2{
        padding:0 175px;
        font-size:1.5rem;
        line-height:2.2rem;
        text-align:center;
        color:#424242;
        margin-bottom:60px;
    }
    &:after {
        content: "";
        background: url(bubbles-claro.png) repeat-x;
        position: absolute;
        bottom: -44px;
        width: 100%;
        height: 45px;
        left:0;
    }
    .btn-enviar{
        background-color: #E65A2B;
        max-width: 20rem;
        margin: auto;
        padding: 1.5rem 0;
        display: none;
        text-align: center;
        font-size: 1.25rem;
        border-bottom: 0.25rem solid #AD4420;
        border-radius: 0.25rem;
        display: block;
    }
}

.box-info-no-image{
    margin:3rem auto 5rem auto;
}

.block-cards{
    margin:5rem 0;
    .container{
        display:flex;
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: space-between;
        .item{
            img{
                max-width:100%;
                height:auto;
            }
            cursor:pointer;
            width:340px;
            height:340px;
            margin-bottom:50px;
            .back{
                display:flex;
                font-size:20px;
                align-items: center;
                justify-content: center;
                flex-direction: column;
                background-color:#B4B0A7;
                height:340px;
                p{
                    padding:20px;
                    color:#fff;
                    text-align:center;
                }
            }
        }
    }
}

.box-image-input{
    background:url('../assets/bg-fase3.jpg');
    .links-pdf{
        a{
            padding:25px 50px;
            background-color:#fff;
            color:#424242;
            text-decoration:none;
            display:block;
            width:calc(100% - 280px);
            margin:auto;
            margin-bottom:20px;
            font-size:20px;
            background:#fff url('../assets/download-red.png') no-repeat center right 20px;
        }
    }
    h2, h3{
        color:#fff;
        text-shadow: #424242 1px 1px 2px;
    }

    h2{
        font-size:24px;
        line-height:30px;
        max-width:800px;
        margin:auto;;
    }

    h3{
        font-size:20px;
        margin-bottom:40px;
    }
}